html{
  height: 100%;
 }

body {
  width: 75%;
  height: 100%;

  background: 
    -webkit-linear-gradient(135deg, rgba(0,0,0,0.3), rgba(0, 40, 105, 0.8)),
    url('./Static/img/2.jpeg');

  background: 
    -o-linear-gradient(135deg, rgba(0,0,0,0.3), rgba(0, 40, 105, 0.8)),
    url('./Static/img/2.jpeg');

  background: 
    linear-gradient(-45deg, rgba(0,0,0,0.3), rgba(0, 40, 105, 0.8)),
    url('./Static/img/2.jpeg');
  background-attachment: fixed;
  background-size: auto; 
}

* {
  height: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: 0;
}

.notesWrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100%;
}

.notesHeader {
  display: -ms-flexbox;
  display: flex;
  float: left;

  min-height: 2.5em;
  max-height: 2em;
  font-size: 3.2em;
  color: #F4B350;
  font-family: 'Permanent Marker', cursive;
  padding-left: 300px;
  -ms-flex-line-pack: center;
      align-content: center;
  padding:20px 0px 20px 0px;
  margin-bottom: 6px;
  background-color: rgba(0,0,0,0.5);
  text-shadow: 3px 3px 10px #000;
  display: block;
}
.subheading {
  float:right;
  font-size: 15px;
  color: #F4B350;
  font-family: 'Permanent Marker', cursive;
  padding:20px 20px 20px 0px;
  margin-bottom: 6px;

  text-shadow: 3px 3px 10px #000;
  display: block;
}
.subheadingleft {
  float:left;
  font-size: 15px;
  color: #F4B350;
  font-family: 'Permanent Marker', cursive;
  padding-left: 400px;
  padding-top: 20px;
  margin-bottom: 6px;

  text-shadow: 3px 3px 10px #000;
  display: block;
}
.notesBody {
  padding-top: 24px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1 85%;
      flex: 1 1 85%;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-line-pack: start;
      align-content: flex-start;
  overflow: auto;
}

.notesFooter {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1 5%;
      flex: 1 1 5%;
  background-color: rgba(0,0,0,0.5);
}
.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  float: left;
  height: 80px;
  -webkit-animation-name: spin;
          animation-name: spin;
  -webkit-animation-duration: 5000ms;
          animation-duration: 5000ms;
  padding-left: 300px;
  padding-right: 0;
  

}
.App-logo-right {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  float: right;
  height: 90px;
  -webkit-animation-name: spin;
          animation-name: spin;
  -webkit-animation-duration: 5000ms;
          animation-duration: 5000ms;
  padding-right: 250px;
  padding-top: 5px;
  
  

}


@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


